<template>
  <v-app>
    <template v-if="!mounting">
      <template v-if="login">
        <navigation v-model="drawer"/>
        <v-app-bar
          app dark clipped-left
          class="black"
        >
          <v-app-bar-nav-icon
            @click="drawer = !drawer"
          ></v-app-bar-nav-icon>
          <v-toolbar-title class="d-flex">
            <v-img
              src="/logo.png"
              width="30" height="30"
              class="mr-2"
            />
            Magnifiscent500
          </v-toolbar-title>
          <v-spacer />
          <v-menu
            bottom
            origin="center center"
            transition="scale-transition"
            :offset-y="true"
            nudge-bottom="10"
            min-width="200px"
          >
            <template v-slot:activator="{ on }">
              <v-btn
                v-on="on"
                class="ml-2 elevation-0"
                fab
                small
              >
                <span class="headline white--text text-uppercase">
                  <small>
                    M
                  </small>
                </span>
              </v-btn>
            </template>
            <v-card class="elevation-0 pa-0">
              <v-card-title class="pa-3">
                <v-avatar
                  class="mx-auto" size="64"
                  color="yellow darken-2"
                >
                  <span
                    class="headline text-uppercase"
                  >
                    M
                  </span>
                </v-avatar>
              </v-card-title>
              <v-card-text class="pa-0">
                <v-list
                  dense
                  rounded
                >
                  <v-list-item
                    link
                    style="border: 1px solid lightgray!important;"
                    class="grey lighten-4"
                  >
                    <v-list-item-content>
                      <v-list-item-title
                        v-if="profile.role"
                        class="text-center text-capitalize"
                        style="font-size: 14px;"
                      >
                        {{ profile.role.name }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item
                    link
                    active-class="primary white--text"
                  >
                    <v-list-item-action>
                      <v-icon style="font-size: 20px;">fas fa-user-circle</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title
                        class="text-left"
                        style="font-size: 14px;"
                      >
                        Profile
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item
                    @click="logOut()"
                    link active-class="primary white--text"
                  >
                    <v-list-item-action>
                      <v-icon style="font-size: 20px;">fas fa-sign-out-alt</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title
                        class="text-left"
                        style="font-size: 14px;"
                      >
                        Log Out
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-card-text>
            </v-card>
          </v-menu>
        </v-app-bar>
      </template>
      <v-main class="grey lighten-3">
        <router-view></router-view>
      </v-main>
      <v-snackbar
        v-model="snackbar"
        :timeout="2000"
        top right
      >
        {{ text }}
        <template v-slot:action="{ attrs }">
          <v-btn
            color="blue"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </template>
    <page-loading v-else />
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'
import Navigation from '@/components/Navigation'
import PageLoading from '@/components/PageLoading'

export default {
  name: 'App',
  components: {
    Navigation,
    PageLoading
  },
  data: () => ({
    drawer: null,
    mounting: true
  }),
  computed: {
    ...mapGetters({
      login: 'login',
      text: 'text',
      profile: 'profile'
    }),
    snackbar: {
      get () {
        return this.$store.getters.snackbar
      },
      set (value) {
        this.$store.commit('setSnackbar', value)
      }
    }
  },
  async mounted () {
    if (this.login) {
      await this.$store.dispatch('getProfile')
      this.mounting = false
    } else {
      this.mounting = false
    }
  },
  methods: {
    async logOut () {
      await this.$axios.post('/log-out')
      this.$cookies.keys().forEach(cookie => this.$cookies.remove(cookie))
      this.$store.commit('reset')
      this.$router.push('/login')
    }
  }
}
</script>

<style>
.yellow-brand-color {
  background-color: linear-gradient(90deg,#bf953f,#fcf6ba,#b38728,#fbf5b7,#aa771c);
}
</style>
