<template>
  <v-navigation-drawer
    v-model="inputValue"
    app dark clipped
    class="grey darken-4"
  >
    <div class="w-full h-full bg-opacity">
      <div class="w-full h-full bg-opacity">
      <v-list-item class="transparent">
        <v-list-item-content>
          <v-list-item-title class="title">
            MARJONE PONTILAR
          </v-list-item-title>
          <v-list-item-subtitle>
            rodnie.lecea@gmail.com
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list dense nav>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          :to="item.path"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <v-divider></v-divider>
      <v-btn primary class="py-4" style="width:100%">
        Select Account
      </v-btn>
      <v-list>
        <v-list-item style="font-size:15px">
          Active Account: Marjone Pontilar
        </v-list-item>
      </v-list>
    </div>
  </div>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    value: {
      type: Boolean,
      required: false
    }
  },
  data: () => ({
    adminItems: [
      {
        title: 'Dashboard',
        icon: 'fas fa-tachometer-alt',
        path:'/admin/dashboard'
      }
    ],
    sellerItems: [
      {
        title: 'Dashboard',
        icon: 'fas fa-tachometer-alt',
        path:'/seller/dashboard'
      }
    ],
    userItems: [
      {
        title: 'Dashboard',
        icon: 'fas fa-tachometer-alt',
        path:'/user/dashboard'
      },
      {
        title: 'Direct Referral',
        icon: 'fas fa-users',
        path: '/user/direct-referral'
      },
      {
        title: 'Encashment',
        icon: 'fas fa-money-check',
        path:'/user/encashment'
      },
      {
        title: 'Table of Exit',
        icon: 'fas fa-table',
        path:'/user/table-of-exit'
      },
      {
        title: 'Transaction',
        icon: 'fas fa-handshake',
        path:'/user/transaction'
      },
      {
        title: 'Notification',
        icon: 'far fa-bell',
        path: '/user/notification'
      },
      {
        title: 'Account Summary',
        icon: 'fas fa-file-alt',
        path:'/user/account-summary'
      },
      {
        title: 'Tickets',
        icon: 'fas fa-ticket-alt',
        path:'/user/ticket'
      },
      {
        title: 'Settings',
        icon: 'fas fa-cog',
        path:'/settings'
      }
    ]
  }),
  computed: {
    ...mapGetters({
      role: 'role'
    }),
    items () {
      switch (this.role) {
        case 'Admin':
          return this.adminItems
        case 'Seller':
          return this.sellerItems
        default:
          return this.userItems
      }
    },
    inputValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
      }
    }
  }
}
</script>

<style scoped>
    a {
        font-size: 15px;
        color: #757575  !important;
        text-decoration: none;
    }
    i {
      width: 10px;
      margin-left: 15px;
    }
</style>